<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-s-operation"></i> 学校组织管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-radio-group size="small" v-model="choosed">
                      <el-radio-button label="院系管理"></el-radio-button>
                      <el-radio-button label="入学年份管理"></el-radio-button>
                    </el-radio-group>
                    <el-button v-if="choosed=='院系管理'" class="fr" type="primary" @click="editVisible=true" size="small">新建院系</el-button>
                    <el-button v-else class="fr" type="primary" @click="editVisible3=true" size="small">新建入学年份</el-button>
                </div>
                <div class="cont">
                    <div class="onebg"  v-loading="loading" v-if="choosed=='院系管理'">
                      <el-table
                        :data="tableData5"
                        :show-header="false"
                        style="width: 100%">
                        <el-table-column type="expand" >
                          <template slot-scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                              <div class="class-bg" v-for="item in props.row.classList">
                                <p class="fl">班级 {{item.name}}</p>
                                <div class="fr">
                                  <el-button
                                    size="mini"
                                    @click="editcalss(item)">编辑</el-button>
                                  <el-button
                                    size="mini"
                                    plain
                                    type="danger"
                                    @click="delclass(item.id)">删除</el-button>
                                </div>
                              </div>
                            </el-form>
                          </template>
                        </el-table-column>
                        <el-table-column
                          prop="name">
                        </el-table-column>
                        <el-table-column>
                          <template slot-scope="scope">
                            <div class="fr college">
                              <el-button
                              size="mini"
                              type="primary"
                              plain
                              @click="addclassbutton(scope.row.id)">新建班级</el-button>
                              <el-button
                              size="mini"
                              @click="editxueyuan(scope.row)">编辑</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              plain
                              @click="delworks(scope.row.id)">删除</el-button>
                            </div>
                          </template>
                        </el-table-column>
                      </el-table>

                        <div class="pagination">
                          <el-pagination
                            background
                            :page-size="pageSize"
                            @current-change="changePage"
                            layout="prev, pager, next"
                            :total="total">
                          </el-pagination>
                      </div>
                    </div>

                    <Aschoolmanage2 v-else :propname="propname"/>

                </div>
            </div>
        </div>
        <!-- 新建院系 -->
        <el-dialog title="新建院系" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="newname" size="small" placeholder="院系名称"></el-input>
            <p class="red">* 院系名称不能与现有院系重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Addacademy">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible=false" plain>取 消</el-button>
            </div>
        </el-dialog>
        <!-- 修改院系 -->
        <el-dialog title="修改院系" class="datas-bg" :visible.sync="editVisible1" width="380px">
            <el-input class="margin10" v-model="edititems.name" size="small" placeholder="院系名称"></el-input>
            <p class="red">* 院系名称不能与现有院系重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Renameacademy">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible1=false" plain>取 消</el-button>
            </div>
        </el-dialog>
        <!-- 新建班级 -->
        <el-dialog title="新建班级" class="datas-bg" :visible.sync="editVisible2" width="380px">
            <el-input class="margin10" v-model="classname" size="small" placeholder="班级名称"></el-input>
            <p class="red">* 班级名称不能与现有班级重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Addclass">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible2=false" plain>取 消</el-button>
            </div>
        </el-dialog>
        <!-- 编辑班级 -->
        <el-dialog title="编辑班级" class="datas-bg" :visible.sync="editVisible4" width="380px">
            <el-input class="margin10" v-model="classItems.name" size="small" placeholder="班级名称"></el-input>
            <p class="red">* 班级名称不能与现有班级重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Renameclass">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible4=false" plain>取 消</el-button>
            </div>
        </el-dialog>
        <!-- 新建入学年份 -->
        <el-dialog title="新建入学年份" class="datas-bg" :visible.sync="editVisible3" width="380px">
            <el-input class="margin10" v-model="newyear" size="small" placeholder="年份"></el-input>
            <p class="red">* 年份只能是数字</p>
            <p class="red">* 新建年份不能与现有年份重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Addenrollmentyear">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible3=false" plain>取 消</el-button>
            </div>
        </el-dialog>
    </div> 
</template>

<script>
import {fetchGetacademylist, fetchAddacademy, fetchRenameacademy, fetchDeleteacademy, fetchAddenrollmentyear, fetchAddclass, fetchDeleteclass, fetchRenameclass} from "../../axios/requests";
import Aschoolmanage2 from './Aschoolmanage2.vue'
export default {
    name: 'course',
    components:{Aschoolmanage2},
    data() { 
        return {
          loading:false,
          pageNum:1,
          pageSize:10,
          total:0,

          newname:'',
          edititems:'',
          newyear:'',
          propname:'',

          academyId:'',
          classname:'',
          classItems:'',

          editVisible:false,
          editVisible1:false,
          editVisible2:false,
          editVisible3:false,
          editVisible4:false,

          input:'',
          choosed:'院系管理',
          items:[1,1,1],
          tableData5: []
        };
    },
    mounted(){
      this.Getacademylist();
    },
    methods: {
      delworks(id){
        this.$confirm('确定要删除吗？', '提示', {
            type: 'warning'
        })
        .then(() => {
            this.Deleteacademy(id)
        })
        .catch(() => {});
      },
      changePage(page){
        this.pageNum = page;
        this.Getadminlist();
      },
      addclassbutton(id){//新建班级按钮
        console.log(id)
        this.editVisible2 = true;
        this.academyId = id;
      },
      delclass(id){
        this.$confirm('确定要删除吗？', '提示', {
            type: 'warning'
        })
        .then(() => {
            this.Deleteclass(id)
        })
        .catch(() => {});
      },
      editcalss(e){
        this.editVisible4 = true;
        this.classItems = Object.assign({}, e);
      },
      async Renameclass(){//修改学院
        let {classItems} = this;
        if (!classItems.name) {this.$message.error('班级名不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchRenameclass(classItems.name, classItems.id);
          if (response.status.code ==0) {
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.editVisible4 = false;
            this.Getacademylist()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      async Deleteclass(id) {//删除班级
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeleteclass(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Getacademylist();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
      async Addclass(){//新建班级
        let {classname, academyId} = this;
        if (!classname) {this.$message.error('班级名不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchAddclass(academyId, classname);
          if (response.status.code ==0) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.editVisible2 = false;
            this.Getacademylist()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      async Getacademylist() {//得到学院
          this.loading = true;
          let {pageNum, pageSize} = this;
          try {
            let response = await fetchGetacademylist(pageNum, pageSize);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.tableData5 = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
      },
      async Addacademy(){//新建学院
        let {newname} = this;
        if (!newname) {this.$message.error('学院名不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchAddacademy(newname);
          if (response.status.code ==0) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.editVisible = false;
            this.Getacademylist()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      async Addenrollmentyear(){//新建入学年份
        let {newyear} = this;
        if (!newyear) {this.$message.error('学院名不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchAddenrollmentyear(newyear);
          if (response.status.code ==0) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.editVisible3 = false;
            this.propname = newyear;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      editxueyuan(e){
        this.edititems = Object.assign({}, e);
        this.editVisible1 = true;
      },
      async Renameacademy(){//修改学院
        let {edititems} = this;
        if (!edititems.name) {this.$message.error('学院名不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchRenameacademy(edititems.id, edititems.name);
          if (response.status.code ==0) {
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.editVisible1 = false;
            this.Getacademylist()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      async Deleteacademy(id) {//删除院系
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeleteacademy(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Getacademylist();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
    }
};
</script>
<style lang='scss'>
  .cont{
      tbody{
        .el-table__expanded-cell{
          padding:5px 0 5px 60px;
        }
      }
    }
</style>
<style lang='scss' scoped>
    .red{
      color:red;
      font-size:12px;
      line-height:16px;
      padding-top:5px;
    }
    .cont{
      padding-top:20px;
      .class-bg{
        overflow:hidden;
        line-height:32px;
        font-size:13px;
        color:#666;
      }
      .college{
        margin-right:-10px;
      }
    }
    .pagination{
        margin-top:20px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
</style>


