<template>
    <div class="twobg"  v-loading="loading">
        <el-table
          :data="tableData2"
          :show-header="false"
          style="width: 100%;">
          <el-table-column
            type="index"
            width="100px">
          </el-table-column>
          <el-table-column
            prop="enrollmentYear">
          </el-table-column>
          <el-table-column  width='150px'>
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="edityear(scope.row)">编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  plain
                  @click="delworks(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
              background
              :page-size="pageSize"
              @current-change="changePage"
              layout="prev, pager, next"
              :total="total">
            </el-pagination>
        </div>
        <!-- 编辑入学年份 -->
        <el-dialog title="修改入学年份" class="datas-bg" :visible.sync="editVisible3" width="380px">
            <el-input class="margin10" v-model="edititems.enrollmentYear" size="small" placeholder="年份"></el-input>
            <p class="red">* 年份只能是数字</p>
            <p class="red">* 年份不能与现有年份重复</p>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Updateenrollmentyear">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible3=false" plain>取 消</el-button>
            </div>
        </el-dialog>
      </div>
</template>

<script>
import {fetchListenrollmentyear, fetchUpdateenrollmentyear, fetchDeleteenrollmentyear} from '../../axios/requests'
export default {
    name: 'course',
    props: ["propname"],
    data() {
        return {
          loading:false,
          pageNum:1,
          pageSize:10,
          total:0,

          edititems:'',

          tableData2:[
            {title:2018},
            {title:2019}
          ],
          editVisible3:false
        };
    },
    watch:{
      propname: {
          handler(newVal, oldVal) {
              this.Listenrollmentyear()
          },
          deep: true
        }
      },
    mounted(){
      this.Listenrollmentyear();
    },
    methods: {
        delworks(id){
          this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Deleteenrollmentyear(id)
            })
            .catch(() => {});
        },
        changePage(page){
          this.pageNum = page;
          this.Listenrollmentyear();
        },
        async Listenrollmentyear() {//得到入学年份列表
          this.loading = true;
          let {pageNum, pageSize} = this;
          try {
            let response = await fetchListenrollmentyear(pageNum, pageSize);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.tableData2 = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
       },
       edityear(e){
          this.edititems = Object.assign({}, e);
          this.editVisible3 = true;
       },
       async Updateenrollmentyear(){//修改入学年份
        let {edititems} = this;
        if (!edititems.enrollmentYear) {this.$message.error('入学年份不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchUpdateenrollmentyear(edititems.id, edititems.enrollmentYear);
          if (response.status.code ==0) {
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.editVisible3 = false;
            this.Listenrollmentyear()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      async Deleteenrollmentyear(id) {//删除入学年份
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeleteenrollmentyear(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Listenrollmentyear();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
    }
};
</script>
<style lang='scss'>
  .cont{
      tbody{
        .el-table__expanded-cell{
          padding:5px 0 5px 60px;
        }
      }
    }
</style>
<style lang='scss' scoped>
    .red{
      color:red;
      font-size:12px;
      line-height:16px;
      padding-top:5px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
    .cont{
      padding-top:20px;
      .class-bg{
        overflow:hidden;
        line-height:32px;
        font-size:13px;
        color:#666;
      }
      .college{
        margin-right:-10px;
      }
    }
    .pagination{
        margin-top:20px;
    }
</style>


